.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.main{
    background: #6A0D0F;
    height: 50px;
    width: 30%;
    padding: 50px;
    color: #FFFFFF;
}

.controls{
    display: flex;
    justify-content: space-between;
    gap: 300px;
    margin-top: 50px;
}

.admin,.user{
    background: #312161;
    color: #FFFFFF;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
}

@media (max-width:600px){
    .main{
        width: 50%;
    }
    .controls{
        gap: 50px;
    }
}